    <div class="step1_agreement_button">
      <mu-checkbox
        label="同意用户协议"
        v-model="formData.isAgree"
      ></mu-checkbox>
    </div>
    <div>
      <mu-button
        flat
        class="demo-step-button"
        :disabled="activeStep === 0"
        @click="handlePrev"
      >
        上一步
      </mu-button>
      <mu-button
        class="demo-step-button"
        color="primary"
        :disabled="!formData.isAgree"
        @click="handleNext"
      >
       下一步
      </mu-button>
