}
.main-step1-content-scroll {
  height: 100%;
  width: 8px;
  position: absolute;
  top: 0;
  right: 0;
  visibility: hidden;
}
.main-step1-content:hover .main-step1-content-scroll {
  visibility: visible;
}
.main-step1-content-scroll-wrap {
  height: 100%;
  width: 8px;
  background: #f3f3f3;
  border-radius: 5px;
  position: relative;
}
.main-step1-content-scroll-point {
  width: 8px;
  height: 100px;
  background: #ccc;
  border-radius: 5px;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}
.main-button {
  text-align: center;
  margin-top: 20px;
}
.agreement-button {
  display: inline-block;
  padding: 10px 20px;
  color: #fff;
  background: #1e9fff;
  border-radius: 4px;
  margin: 0 10px;
  cursor: pointer;
  border: none;
}
.agreement-button:hover {
  background: #248ee8;
}
.agreement-button-gray {
  background: #d8d8d8 !important;
}
.agreement-button-gray:hover {
  background: gray;
}
.agreement-button[disabled="disabled"] {
  background: #ccc;
  cursor: wait;
}

.step1_agreement_button {
  margin: 20px 0;
}
</style>



